import React, { useEffect, useState } from 'react';
import './Login.css'
import { useForm } from "react-hook-form";
import { useSelector, useDispatch, connect } from 'react-redux';
import {login, SignInGoogle} from '../../actions/UserAction'
import { useHistory } from 'react-router';
import {Link} from 'react-router-dom'
import {EyeOutlined, EyeInvisibleOutlined } from '@ant-design/icons'
import { blockSpace } from '../../untils';


function Login (props) {
  const dispatch = useDispatch();
  const history = useHistory();
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();

  const [state, setState] = useState(false)
  const show = () =>{
    setState(!state)
  }
  const user = useSelector((state) => state.userSignin);
  const { userInfo, error } = user;
 

  const login1 = () =>{
    if(email1 == "undefined" || email1 == ""|| email1 ==null){
      SetError2(true)
    }else if(pass1 == "undefined"|| pass1 == ""|| pass1 ==null){
      SetError2(true)
    }else{
      SetError2(false)
    }
  }

  const onSubmit = (data) => {
   
    dispatch(login(data));
    
  };

  useEffect(() => {
    if (userInfo) {
      history.push("/");
    }
  });

  const SignInFirebase = async() => {
   await dispatch(SignInGoogle())
}

const fotgotPass = () => {
  history.push("/forgotpassword")
}

const [email1,SetEmail1] = useState();
console.log(email1);
const [pass1,SetPass1] = useState()
const [error2,SetError2] = useState(false)
const [error1,SetError1] = useState("Please Fill Out this Field")
  return (
    <div class="login-page">
      <h2> ĐĂNG NHẬP </h2>
     
           <form onSubmit={handleSubmit(onSubmit)} 
      class="form-login">
        <input {...register("email")} placeholder="Email" value={email1} required 
        onChange={e => SetEmail1(e.target.value)}
        onKeyPress={blockSpace}></input>
        <input
         onChange={e => SetPass1(e.target.value)}
          value={pass1}
          className={{positon: 'relative'}}
          {...register("password")}
          placeholder="Password"
          type={state? "text" : "password"}
          required
          onKeyPress={blockSpace}
        ></input>
        <button className='show_hide' onClick={() => show()}>
       {
          state ? <EyeOutlined /> : <EyeInvisibleOutlined />
       }
        </button>
      

        <input type="submit" value="Đăng Nhập" onClick={() => login1()}></input>
        {error ? <h2>{error}</h2> : <></>}
        {error2 ? <h2>{error1}</h2> : <></>}
           {/* <Link to="/register">Tạo tài khoản?</Link> */}
      </form>
      
     
     <div className='google'>
     <button className='btn-google' onClick={() => SignInFirebase()}>
          <img className="img-google" src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/1200px-Google_%22G%22_Logo.svg.png"/>
           Đăng nhập bằng Google</button>
     </div>
       <div className='facebook'>
       <button className='btn-facebook'>
        <img className="img-facebook" src="data:image/png;base64,"/>
           Đăng nhập bằng Facebook</button>
       </div>

       <Link to="/register" className='register'>Tạo tài khoản?</Link>
       <div className='forgot-pass' onClick={fotgotPass}>Quên mật khẩu</div>
    </div>
  );
}


 export default Login;